.InstanceDetails .lblRow {
    display: flex;
}

.InstanceDetails .lblInput {
    width: 100%;
    margin-right: 0.5em;
}

.InstanceDetails .labeledSlider input {
    vertical-align: middle;
}

.InstanceDetails .labeledSlider label {
    vertical-align: middle;
    margin-left: 0.5em;
}

.InstanceDetails .attrTable .attrRow {
    display: flex;
    width: 100%;
    max-width: 100%;
    margin-top: 0.25em;
    margin-bottom: 0.25em;
}

.InstanceDetails .attrTable .attrRow input,
.InstanceDetails .attrTable .attrRow span {
    flex: 1;
    margin-right: 0.5em;
    min-width: 0;
}

.InstanceDetails .attrTable .attrHeader * {
    font-weight: bold;
}