.ImageArea {
    display: flex;
    flex-direction: column;
    padding: 1em;
}

.ImageArea .header {
    flex: 0;
    text-align: center;
    margin-bottom: .5em;
}
.ImageArea .header p {
    margin-top: 0.3em;
    margin-bottom: 0.3em;
}

.ImageArea .imgRoot {
    position: relative;
}

.ImageArea .origImg {
    flex: 1;
    min-height: 0;
    object-fit: contain;
    max-width: 100%;
}

.ImageArea .overlay {
    pointer-events: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.ImageArea .overlay.masks.filter0 {
    filter: brightness(0) saturate(100%) invert(89%) sepia(31%) saturate(4928%) hue-rotate(127deg) brightness(100%) contrast(105%);
}

.ImageArea .overlay.masks.filter1 {
    filter: invert(60%) sepia(27%) saturate(4741%) hue-rotate(2deg) brightness(105%) contrast(104%);
}

.ImageArea .overlay.masks.filter2 {
    filter: invert(41%) sepia(61%) saturate(3938%) hue-rotate(276deg) brightness(105%) contrast(108%);
}