.MaskList {
    flex-shrink: 1;
    min-height: 0;
    display: flex;
    flex-direction: column;
}
.MaskList .entries {
    flex-shrink: 1;
    min-height: 0;
    overflow-y: auto;
    overflow-x: hidden;
}

.MaskEntry.selected {
    background-color: bisque;
}
.MaskEntry {
    border-radius: 0.5em;
    padding: 0.5em;
    margin-top: 0.5em;
    margin-bottom: 0.5em;
    background-color: aliceblue;
    cursor: pointer;
}
.MaskEntry .mainContent {
    display: flex;
}
.MaskEntry .uid {
    font-size: 0.5em;
}
.MaskEntry .mainContent span {
    flex-grow: 1;
}
.MaskEntry.newEntry {
    background-color: greenyellow;
}
.MaskEntry.newEntry span {
    text-align: center;
}
